(function() {
  'use strict';

  angular
    .module('moto', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ngRoute', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('moto')
    .factory('news', news);

      /** @ngInject */

      function news($http) {

        function getData(callback) {
            $http({
              method: 'GET',
              url: 'app/data/articles.json',
              cache: true
            }).success(callback);
        }

        return {
          list: getData,
          find: function (title, callback) {
              getData(function (data) {
                  var news = data.filter(function (entry) {
                      return entry.title === title;
                  })[0];
                  callback(news);
              });
          }
        };

      }

})();

(function() {
  'use strict';

  angular
    .module('moto')
    .controller('MainController', MainController);

  /** @ngInject */

  function MainController($timeout, webDevTec, toastr, news) {

    var vm = this;

    news.list(function(news) {
      vm.news = news;
    });

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1449583820715;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('moto')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('moto')
    .config(routeConfig);

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .otherwise({
        redirectTo: '/'
      });
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('moto')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('moto')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("moto").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=\"container\"><div ng-repeat=\"article in main.news\"><img ng-src=\"{{article.images.img1}}\" alt=\"{{article.title}}\"> <a ng-href=\"news/{{article.category}}/{{article.title}}\">{{article.title}}</a></div><div><acme-navbar creation-date=\"main.creationDate\"></acme-navbar></div><div class=\"jumbotron\"><h1>\'Allo, \'Allo!</h1><p class=\"lead\"><img src=\"assets/images/yeoman.png\" alt=\"I\'m Yeoman\"><br>Always a pleasure scaffolding your apps.</p><p class=\"animated infinite\" ng-class=\"main.classAnimation\"><a class=\"btn btn-lg btn-success\" ng-click=\"main.showToastr()\">Splendid Toastr</a></p><p>With â™¥ thanks to the contributions of<acme-malarkey extra-values=\"[\'Yeoman\', \'Gulp\', \'Angular\']\"></acme-malarkey></p></div><div class=\"col\" ng-repeat=\"awesomeThing in main.awesomeThings | orderBy:\'rank\'\"><div class=\"thumbnail\"><img class=\"pull-right\" ng-src=\"assets/images/{{ awesomeThing.logo }}\" alt=\"{{ awesomeThing.title }}\"><div class=\"caption\"><h3>{{ awesomeThing.title }}</h3><p>{{ awesomeThing.description }}</p><p><a ng-href=\"{{ awesomeThing.url }}\">{{ awesomeThing.url }}</a></p></div></div></div></div>");}]);